import axiosInstance from "@/api/axios/axiosInstance";

// Get all study assignments
export const getAllStudyAssignments = async () => {
  const response = await axiosInstance.get("/api/study-assignments");
  return response.data;
};

// Get study assignment by ID
export const getStudyAssignmentById = async (id: string) => {
  const response = await axiosInstance.get(`/api/study-assignments/${id}`);
  return response.data;
};

// Get study assignments by study ID
export const getStudyAssignmentsByStudyId = async (studyId: string) => {
  const response = await axiosInstance.get(`/api/study-assignments/study/${studyId}`);
  return response.data;
};

// Get study assignments by user ID
export const getStudyAssignmentsByUserId = async (userId: string) => {
  const response = await axiosInstance.get(`/api/study-assignments/user/${userId}`);
  return response.data;
};

// Create study assignment
export const createStudyAssignment = async (assignmentData: any) => {
  const response = await axiosInstance.post("/api/study-assignments", assignmentData);
  return response.data;
};

// Update study assignment
export const updateStudyAssignment = async (id: string, assignmentData: any) => {
  const response = await axiosInstance.put(`/api/study-assignments/${id}`, assignmentData);
  return response.data;
};

// Remove study assignment
export const removeStudyAssignment = async (id: string) => {
  const response = await axiosInstance.delete(`/api/study-assignments/${id}`);
  return response.data;
};

// Get study assignment statistics
export const getStudyAssignmentStats = async () => {
  const response = await axiosInstance.get("/api/study-assignments/stats");
  return response.data;
};

// Get available users for study assignment
export const getAvailableUsersForStudy = async (studyId: string) => {
  const response = await axiosInstance.get(`/api/study-assignments/study/${studyId}/available-users`);
  return response.data;
};

// Bulk assign users to study
export const bulkAssignUsersToStudy = async (studyId: string, assignments: any[]) => {
  const response = await axiosInstance.post(`/api/study-assignments/study/${studyId}/bulk-assign`, {
    assignments
  });
  return response.data;
};
