import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '../../components/ui/card';
import { Button } from '../../components/ui/button';
import { Badge } from '../../components/ui/badge';
import { Input } from '../../components/ui/input';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '../../components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../../components/ui/select';
import { Label } from '../../components/ui/label';
import { Textarea } from '../../components/ui/textarea';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { toast } from 'sonner';
import {
  FileText,
  Plus,
  Building2,
  Users,
  Calendar,
  AlertCircle,
  TrendingUp,
  Clock,
  Search,
  Filter,
  Edit,
  Trash2,
  UserPlus,
  Loader2,
  Settings,
  MapPin
} from 'lucide-react';
import {
  getAllClinicalStudies,
  getClinicalStudyStats,
  createClinicalStudy,
  assignStudyToSite
} from '../../api/services/clinicalStudy/clinicalStudyService';
import { getAllClinicalSites } from '../../api/services/clinicalSite/clinicalSiteService';
import { getAllClinicalUsers } from '../../api/services/clinicalUser/clinicalUserService';
import {
  getStudyAssignmentsByStudyId,
  createStudyAssignment,
  removeStudyAssignment,
  getAvailableUsersForStudy
} from '../../api/services/studyAssignment/studyAssignmentService';
import { getAllClinicalRoles } from '../../api/services/clinicalRole/clinicalRoleService';

const ClinicalStudies = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isAssignDialogOpen, setIsAssignDialogOpen] = useState(false);
  const [selectedStudyForAssignment, setSelectedStudyForAssignment] = useState(null);
  const [selectedSiteForAssignment, setSelectedSiteForAssignment] = useState('');
  const [assignmentNotes, setAssignmentNotes] = useState('');
  const [showFilters, setShowFilters] = useState(false);
  const [selectedStudy, setSelectedStudy] = useState(null);

  // Form state for creating study
  const [formData, setFormData] = useState({
    name: '',
    study_id: '',
    description: '',
    protocol_number: '',
    principal_investigator_id: '',
    study_type: 'interventional',
    phase: 'not_applicable',
    status: 'not_yet_recruiting',
    target_enrollment: '',
    start_date: '',
    end_date: ''
  });

  const queryClient = useQueryClient();

  // Fetch clinical studies
  const { data: studiesData, isLoading: studiesLoading, error: studiesError } = useQuery({
    queryKey: ['clinicalStudies'],
    queryFn: getAllClinicalStudies,
    retry: 1,
  });

  // Fetch study statistics
  const { data: statsData, isLoading: statsLoading } = useQuery({
    queryKey: ['clinicalStudyStats'],
    queryFn: getClinicalStudyStats,
    retry: 1,
  });

  // Fetch clinical sites for dropdown
  const { data: sitesData } = useQuery({
    queryKey: ['clinicalSites'],
    queryFn: getAllClinicalSites,
    retry: 1,
  });

  // Fetch clinical users for PI dropdown
  const { data: usersData } = useQuery({
    queryKey: ['clinicalUsers'],
    queryFn: getAllClinicalUsers,
    retry: 1,
  });

  // Fetch clinical roles for assignment
  const { data: rolesData } = useQuery({
    queryKey: ['clinicalRoles'],
    queryFn: getAllClinicalRoles,
    retry: 1,
  });

  const studies = studiesData?.data || [];
  const studyStats = statsData?.data || {
    total_studies: 0,
    active_studies: 0,
    recruiting_studies: 0,
    completed_studies: 0
  };
  const clinicalSites = sitesData?.data || [];
  const clinicalUsers = usersData?.data || [];
  const clinicalRoles = rolesData?.data || [];

  // Get all users who can be principal investigators (all clinical users)
  const availableInvestigators = clinicalUsers.map(user => ({
    id: user.id,
    name: user.username || `${user.first_name} ${user.last_name}`.trim() || user.email,
    email: user.email
  })).filter(user => user.name);

  // Dropdown data matching database schema
  const studyPhases = [
    { value: "early_phase_1", label: "Early Phase 1" },
    { value: "phase_1", label: "Phase 1" },
    { value: "phase_2", label: "Phase 2" },
    { value: "phase_3", label: "Phase 3" },
    { value: "phase_4", label: "Phase 4" },
    { value: "not_applicable", label: "Not Applicable" }
  ];

  const studyTypes = [
    { value: "interventional", label: "Interventional" },
    { value: "observational", label: "Observational" },
    { value: "expanded_access", label: "Expanded Access" }
  ];

  const studyStatuses = [
    { value: "not_yet_recruiting", label: "Not Yet Recruiting" },
    { value: "recruiting", label: "Recruiting" },
    { value: "enrolling_by_invitation", label: "Enrolling by Invitation" },
    { value: "active_not_recruiting", label: "Active, Not Recruiting" },
    { value: "suspended", label: "Suspended" },
    { value: "terminated", label: "Terminated" },
    { value: "completed", label: "Completed" },
    { value: "withdrawn", label: "Withdrawn" }
  ];

  // Create study mutation
  const createStudyMutation = useMutation({
    mutationFn: createClinicalStudy,
    onSuccess: () => {
      toast.success('Study created successfully');
      queryClient.invalidateQueries({ queryKey: ['clinicalStudies'] });
      queryClient.invalidateQueries({ queryKey: ['clinicalStudyStats'] });
      setIsCreateDialogOpen(false);
      // Reset form
      setFormData({
        name: '',
        study_id: '',
        description: '',
        protocol_number: '',
        principal_investigator_id: '',
        study_type: 'interventional',
        phase: 'not_applicable',
        status: 'not_yet_recruiting',
        target_enrollment: '',
        start_date: '',
        end_date: ''
      });
    },
    onError: (error) => {
      toast.error(error.response?.data?.message || 'Failed to create study');
    },
  });

  // Study assignment mutation
  const assignUserMutation = useMutation({
    mutationFn: createStudyAssignment,
    onSuccess: () => {
      queryClient.invalidateQueries(['studyAssignments']);
      toast.success('User assigned to study successfully!');
      setIsAssignDialogOpen(false);
      setSelectedUserForAssignment('');
      setSelectedRoleForAssignment('');
      setSelectedStudyForAssignment(null);
    },
    onError: (error) => {
      toast.error('Failed to assign user: ' + error.message);
    },
  });

  // Site assignment mutation
  const assignSiteMutation = useMutation({
    mutationFn: ({ studyId, siteId, notes }) => assignStudyToSite(studyId, siteId, notes),
    onSuccess: () => {
      queryClient.invalidateQueries(['clinicalStudies']);
      queryClient.invalidateQueries(['clinicalSites']);
      toast.success('Study assigned to site successfully!');
      setIsAssignDialogOpen(false);
      setSelectedSiteForAssignment('');
      setAssignmentNotes('');
      setSelectedStudyForAssignment(null);
    },
    onError: (error) => {
      toast.error('Failed to assign study to site: ' + error.message);
    },
  });

  const handleCreateStudy = () => {
    // Validation
    if (!formData.name || !formData.study_id || !formData.protocol_number) {
      toast.error('Please fill in all required fields');
      return;
    }

    // Prepare data for API
    const studyData = {
      ...formData,
      principal_investigator_id: formData.principal_investigator_id ? parseInt(formData.principal_investigator_id) : null,
      target_enrollment: formData.target_enrollment ? parseInt(formData.target_enrollment) : 0
    };

    createStudyMutation.mutate(studyData);
  };

  const handleFormChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleAssignSite = () => {
    if (!selectedStudyForAssignment || !selectedSiteForAssignment) {
      toast.error('Please select a clinical site');
      return;
    }

    assignSiteMutation.mutate({
      studyId: selectedStudyForAssignment.id.toString(),
      siteId: selectedSiteForAssignment,
      notes: assignmentNotes
    });
  };

  const openAssignDialog = (study) => {
    setSelectedStudyForAssignment(study);
    setSelectedSiteForAssignment('');
    setAssignmentNotes('');
    setIsAssignDialogOpen(true);
  };

  // Handler functions for button functionality
  const handleEditStudy = (study) => {
    setSelectedStudy(study);
    // Populate form with existing data
    setFormData({
      name: study.name || '',
      study_id: study.study_id || '',
      description: study.description || '',
      protocol_number: study.protocol_number || '',
      principal_investigator_id: study.principal_investigator_id?.toString() || '',
      study_type: study.study_type || 'interventional',
      phase: study.phase || 'not_applicable',
      status: study.status || 'planning',
      start_date: study.start_date || '',
      end_date: study.end_date || '',
      target_enrollment: study.target_enrollment || ''
    });
    setIsCreateDialogOpen(true);
  };

  const handleManageStudy = (study) => {
    toast.info(`Opening management panel for "${study.name}"`);
    // TODO: Implement study management functionality
  };

  const handleViewParticipants = (study) => {
    toast.info(`Viewing participants for "${study.name}"`);
    // TODO: Navigate to participants page or open participants modal
  };

  const handleViewTimeline = (study) => {
    toast.info(`Viewing timeline for "${study.name}"`);
    // TODO: Open timeline view or navigate to timeline page
  };

  const handleViewDocuments = (study) => {
    toast.info(`Viewing documents for "${study.name}"`);
    // TODO: Open documents panel or navigate to documents page
  };

  const toggleFilters = () => {
    setShowFilters(!showFilters);
  };

  const getStatusBadge = (status) => {
    const statusColors = {
      'recruiting': 'bg-blue-100 text-blue-800',
      'not_yet_recruiting': 'bg-yellow-100 text-yellow-800',
      'active': 'bg-green-100 text-green-800',
      'suspended': 'bg-yellow-100 text-yellow-800',
      'completed': 'bg-gray-100 text-gray-800',
      'terminated': 'bg-red-100 text-red-800'
    };
    return statusColors[status] || 'bg-gray-100 text-gray-800';
  };

  const getPhaseBadge = (phase) => {
    const phaseColors = {
      'phase_1': 'bg-purple-100 text-purple-800',
      'phase_2': 'bg-blue-100 text-blue-800',
      'phase_3': 'bg-green-100 text-green-800',
      'phase_4': 'bg-orange-100 text-orange-800',
      'post_market': 'bg-gray-100 text-gray-800'
    };
    return phaseColors[phase] || 'bg-gray-100 text-gray-800';
  };

  const formatPhase = (phase) => {
    const phaseLabels = {
      'phase_1': 'Phase I',
      'phase_2': 'Phase II',
      'phase_3': 'Phase III',
      'phase_4': 'Phase IV',
      'post_market': 'Post Market'
    };
    return phaseLabels[phase] || phase;
  };

  const formatStatus = (status) => {
    const statusLabels = {
      'recruiting': 'Recruiting',
      'not_yet_recruiting': 'Not Yet Recruiting',
      'active': 'Active',
      'suspended': 'Suspended',
      'completed': 'Completed',
      'terminated': 'Terminated'
    };
    return statusLabels[status] || status;
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Clinical Studies</h1>
          <p className="text-gray-600 mt-1">Manage clinical research studies and trials</p>
        </div>
        
        <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
          <DialogTrigger asChild>
            <Button>
              <Plus className="h-4 w-4 mr-2" />
              Create New Study
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto" aria-describedby="create-study-description">
            <DialogHeader>
              <DialogTitle>{selectedStudy ? 'Edit Clinical Study' : 'Create New Clinical Study'}</DialogTitle>
              <DialogDescription id="create-study-description">
                {selectedStudy ? 'Update the clinical research study information.' : 'Fill out the form below to create a new clinical research study. All required fields must be completed.'}
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="studyName">Study Name *</Label>
                  <Input
                    id="studyName"
                    placeholder="Enter study name"
                    value={formData.name}
                    onChange={(e) => handleFormChange('name', e.target.value)}
                  />
                </div>
                <div>
                  <Label htmlFor="studyCode">Study Code *</Label>
                  <Input
                    id="studyCode"
                    placeholder="e.g., STUDY2024"
                    value={formData.study_id}
                    onChange={(e) => handleFormChange('study_id', e.target.value)}
                  />
                </div>
              </div>

              <div>
                <Label htmlFor="description">Description</Label>
                <Textarea
                  id="description"
                  placeholder="Enter study description"
                  rows={3}
                  value={formData.description}
                  onChange={(e) => handleFormChange('description', e.target.value)}
                />
              </div>

              <div>
                <Label htmlFor="pi">Principal Investigator</Label>
                <Select value={formData.principal_investigator_id} onValueChange={(value) => handleFormChange('principal_investigator_id', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select PI (optional)" />
                  </SelectTrigger>
                  <SelectContent>
                    {availableInvestigators.map((pi) => (
                      <SelectItem key={pi.id} value={pi.id.toString()}>
                        {pi.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="grid grid-cols-3 gap-4">
                <div>
                  <Label htmlFor="type">Study Type</Label>
                  <Select value={formData.study_type} onValueChange={(value) => handleFormChange('study_type', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Select type" />
                    </SelectTrigger>
                    <SelectContent>
                      {studyTypes.map((type) => (
                        <SelectItem key={type.value} value={type.value}>{type.label}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="phase">Phase</Label>
                  <Select value={formData.phase} onValueChange={(value) => handleFormChange('phase', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Select phase" />
                    </SelectTrigger>
                    <SelectContent>
                      {studyPhases.map((phase) => (
                        <SelectItem key={phase.value} value={phase.value}>{phase.label}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="targetEnrollment">Target Enrollment</Label>
                  <Input
                    id="targetEnrollment"
                    type="number"
                    placeholder="100"
                    value={formData.target_enrollment}
                    onChange={(e) => handleFormChange('target_enrollment', e.target.value)}
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="startDate">Start Date</Label>
                  <Input
                    id="startDate"
                    type="date"
                    value={formData.start_date}
                    onChange={(e) => handleFormChange('start_date', e.target.value)}
                  />
                </div>
                <div>
                  <Label htmlFor="endDate">Expected End Date</Label>
                  <Input
                    id="endDate"
                    type="date"
                    value={formData.end_date}
                    onChange={(e) => handleFormChange('end_date', e.target.value)}
                  />
                </div>
              </div>

              <div>
                <Label htmlFor="protocolNumber">Protocol Number *</Label>
                <Input
                  id="protocolNumber"
                  placeholder="Enter protocol number"
                  value={formData.protocol_number}
                  onChange={(e) => handleFormChange('protocol_number', e.target.value)}
                />
              </div>

              <div className="flex justify-end space-x-2 pt-4">
                <Button variant="outline" onClick={() => {
                  setIsCreateDialogOpen(false);
                  setSelectedStudy(null);
                  setFormData({
                    name: '',
                    study_id: '',
                    description: '',
                    protocol_number: '',
                    principal_investigator_id: '',
                    study_type: 'interventional',
                    phase: 'not_applicable',
                    status: 'planning',
                    start_date: '',
                    end_date: '',
                    target_enrollment: ''
                  });
                }}>
                  Cancel
                </Button>
                <Button
                  onClick={handleCreateStudy}
                  disabled={createStudyMutation.isPending}
                >
                  {createStudyMutation.isPending ? (
                    <>
                      <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                      {selectedStudy ? 'Updating...' : 'Creating...'}
                    </>
                  ) : (
                    <>
                      {selectedStudy ? <Edit className="h-4 w-4 mr-2" /> : <Plus className="h-4 w-4 mr-2" />}
                      {selectedStudy ? 'Update Study' : 'Create Study'}
                    </>
                  )}
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>

        {/* Study Site Assignment Dialog */}
        <Dialog open={isAssignDialogOpen} onOpenChange={setIsAssignDialogOpen}>
          <DialogContent className="max-w-md" aria-describedby="assign-site-description">
            <DialogHeader>
              <DialogTitle>Assign Study to Site</DialogTitle>
              <DialogDescription id="assign-site-description">
                Assign {selectedStudyForAssignment?.name} to a clinical site for patient recruitment.
              </DialogDescription>
            </DialogHeader>

            <div className="space-y-4">
              <div>
                <Label htmlFor="site-select">Select Clinical Site</Label>
                <Select value={selectedSiteForAssignment} onValueChange={setSelectedSiteForAssignment}>
                  <SelectTrigger>
                    <SelectValue placeholder="Choose a clinical site" />
                  </SelectTrigger>
                  <SelectContent>
                    {clinicalSites.map((site) => (
                      <SelectItem key={site.id} value={site.id.toString()}>
                        {site.site_name} ({site.site_code})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div>
                <Label htmlFor="notes">Assignment Notes (Optional)</Label>
                <textarea
                  id="notes"
                  className="w-full p-2 border border-gray-300 rounded-md"
                  rows="3"
                  placeholder="Add any notes about this site assignment..."
                  value={assignmentNotes}
                  onChange={(e) => setAssignmentNotes(e.target.value)}
                />
              </div>

              <div className="flex justify-end space-x-2 pt-4">
                <Button variant="outline" onClick={() => setIsAssignDialogOpen(false)}>
                  Cancel
                </Button>
                <Button
                  onClick={handleAssignSite}
                  disabled={!selectedSiteForAssignment || assignSiteMutation.isPending}
                >
                  {assignSiteMutation.isPending ? (
                    <>
                      <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                      Assigning...
                    </>
                  ) : (
                    <>
                      <MapPin className="h-4 w-4 mr-2" />
                      Assign to Site
                    </>
                  )}
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Studies</CardTitle>
            <FileText className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{studyStats.total_studies}</div>
            )}
            <p className="text-xs text-muted-foreground">Across all sites</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Active Studies</CardTitle>
            <TrendingUp className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{studyStats.active_studies}</div>
            )}
            <p className="text-xs text-muted-foreground">Currently active</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Recruiting</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{studyStats.recruiting_studies}</div>
            )}
            <p className="text-xs text-muted-foreground">Open for enrollment</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Completed</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{studyStats.completed_studies}</div>
            )}
            <p className="text-xs text-muted-foreground">Finished studies</p>
          </CardContent>
        </Card>
      </div>

      {/* Studies List */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle>Clinical Studies Management</CardTitle>
            <div className="flex space-x-2">
              <div className="relative">
                <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search studies..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-8 w-64"
                />
              </div>
              <Button variant="outline" size="sm" onClick={toggleFilters}>
                <Filter className="h-4 w-4 mr-2" />
                Filter
              </Button>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {studiesLoading ? (
            <div className="flex items-center justify-center py-12">
              <Loader2 className="h-8 w-8 animate-spin text-gray-400" />
              <span className="ml-2 text-gray-600">Loading clinical studies...</span>
            </div>
          ) : studiesError ? (
            <div className="text-center py-12">
              <AlertCircle className="h-12 w-12 text-red-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Error Loading Studies</h3>
              <p className="text-gray-600 mb-4">Failed to load clinical studies. Please try again.</p>
            </div>
          ) : studies.length === 0 ? (
            <div className="text-center py-12">
              <FileText className="h-12 w-12 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">No Studies Found</h3>
              <p className="text-gray-600 mb-4">No clinical studies have been created yet.</p>
              <Button onClick={() => setIsCreateDialogOpen(true)}>
                <Plus className="h-4 w-4 mr-2" />
                Create First Study
              </Button>
            </div>
          ) : (
            <div className="space-y-6">
              {studies.map((study) => (
              <div key={study.id} className="border border-gray-200 rounded-lg p-6 hover:shadow-md transition-shadow">
                <div className="flex justify-between items-start mb-4">
                  <div>
                    <h3 className="text-lg font-semibold text-gray-900">{study.name}</h3>
                    <p className="text-sm text-gray-500">Protocol: {study.protocol_number}</p>
                  </div>
                  <div className="flex items-center space-x-2">
                    <Badge className={getStatusBadge(study.status)}>
                      {formatStatus(study.status)}
                    </Badge>
                    <Badge className={getPhaseBadge(study.phase)}>
                      {formatPhase(study.phase)}
                    </Badge>
                    <div className="flex space-x-1">
                      <Button variant="outline" size="sm" onClick={() => handleEditStudy(study)}>
                        <Edit className="h-4 w-4 mr-1" />
                        Edit
                      </Button>
                      <Button variant="outline" size="sm" onClick={() => openAssignDialog(study)}>
                        <MapPin className="h-4 w-4 mr-1" />
                        Assign to Site
                      </Button>
                      <Button variant="outline" size="sm" onClick={() => handleManageStudy(study)}>
                        <Settings className="h-4 w-4 mr-1" />
                        Manage
                      </Button>
                    </div>
                  </div>
                </div>
                
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-4">
                  <div className="flex items-start space-x-2">
                    <Building2 className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Site</p>
                      <p className="text-sm text-gray-600">{study.site}</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-2">
                    <FileText className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Type</p>
                      <p className="text-sm text-gray-600">{study.type}</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-2">
                    <Calendar className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Start Date</p>
                      <p className="text-sm text-gray-600">{study.startDate}</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-2">
                    <Users className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Principal Investigator</p>
                      <p className="text-sm text-gray-600">{study.principalInvestigator}</p>
                    </div>
                  </div>
                </div>
                
                {/* Enrollment Progress */}
                <div className="mb-4">
                  <div className="flex justify-between items-center mb-2">
                    <span className="text-sm font-medium">Enrollment Progress</span>
                    <span className="text-sm text-gray-600">
                      {study.currentEnrollment} / {study.targetEnrollment} participants
                    </span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div 
                      className="bg-blue-600 h-2 rounded-full" 
                      style={{ width: `${(study.currentEnrollment / study.targetEnrollment) * 100}%` }}
                    ></div>
                  </div>
                </div>
                
                <div className="flex items-center justify-between pt-4 border-t border-gray-100">
                  <div className="flex items-center space-x-4">
                    <span className="text-sm text-gray-600">
                      <strong>0%</strong> Enrolled
                    </span>
                    <span className="text-sm text-gray-600">
                      Target: <strong>{study.targetEnrollment}</strong>
                    </span>
                  </div>
                  <div className="flex space-x-2">
                    <Button variant="outline" size="sm" onClick={() => handleViewParticipants(study)}>
                      <Users className="h-4 w-4 mr-1" />
                      Participants
                    </Button>
                    <Button variant="outline" size="sm" onClick={() => handleViewTimeline(study)}>
                      <Clock className="h-4 w-4 mr-1" />
                      Timeline
                    </Button>
                    <Button variant="outline" size="sm" onClick={() => handleViewDocuments(study)}>
                      <FileText className="h-4 w-4 mr-1" />
                      Documents
                    </Button>
                  </div>
                </div>
              </div>
            ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Development Notice */}
      <Card className="border-yellow-200 bg-yellow-50">
        <CardContent className="flex items-center space-x-3 pt-6">
          <AlertCircle className="h-5 w-5 text-yellow-600" />
          <div>
            <h3 className="font-medium text-yellow-800">Study Management System</h3>
            <p className="text-sm text-yellow-700">
              This is a preview of the Clinical Studies management system. Full functionality including 
              study creation, participant enrollment, timeline management, and document handling will be 
              implemented in the next phase.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ClinicalStudies;
